
body {
    margin: 0;
    font-family: 'Lexend', sans-serif;   
  }

  .home {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
  }
    
  .landing-content {
    max-width: 540px;
    margin: 0;
  }
  
  /* Text Container Styles */
  .text-container {
    text-align: start;
    line-height: 1;
    margin-bottom: 60px;
  }
  
  .tagline {
    color: white;
    font-size: 2.8rem;
    line-height: 3.4rem;
    margin-bottom: 30px;
  }
  
  .description {
    color: white;
    font-size: 2.0rem;
    line-height: 2.4rem;
    letter-spacing: -1px;
    word-spacing: 2px;
    margin-right: -140px
  }
  
  /* Buttons Container Styles */
.buttons-container {
  display: flex;
  justify-content: flex-start;
  margin-top: 60px;
}


.buttons-container a {
  margin-right: 10%;
}

button {
  background: linear-gradient(300deg,
          hsl(248deg 100% 77%) 72%,
          hsl(239deg 100% 84%) 102%,
          hsl(232deg 100% 89%) 100%);
  -webkit-box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 1);
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 1);
  color: white;
  font-family: 'Lexend', sans-serif;
  border: 2px solid transparent;
  border-radius: 10px;
  width: 200px;
  height: 50px;
  border: none;
  font-size: 1.2rem;
  font-weight: 900;
  transition: all 0.1s ease;
}

button:active {
  -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.75);
  transform: translateY(3px); 
}

button:hover {
  cursor: pointer; 
}

.contact-button {
  background: linear-gradient(120deg,
          hsl(0deg 0% 100%) 45%,
          hsl(0, 1%, 86%) 91%,
          hsl(0, 1%, 84%) 100%);
  color: #110030;
}

  
  /* Media Queries for Responsiveness */
  @media (max-width: 600px) {

    .navbar {
      width: 100%;
      margin-bottom: 0;
    }

    #root > div.content-container > div > div.home {
      padding-top: 50px;
    }

    .landing-content {
      padding: 10px;
    }

    .text-container {
      margin-left: 10px;
      margin-right: 10px;
    }
  
    .buttons-container {
      flex-direction: column;
      align-items: center;
      width: 100%;
      > a {
        margin-right: 0;
      }
    }

  
    button {
      margin: 0;
      margin-bottom: 20px;
    }
  }

  @media (max-width: 1025px) {

  .description {
    margin-right: 0px;
  }
  
}


@media (max-width: 1025px) {
  .tagline {
    font-size: 2.2rem;
    line-height: 2rem;
  }

  .description {
    font-size: 1.2rem;
    line-height: 1.2rem;
  }

  button {
    width: 300px;
  }
}
  
  @media (max-width: 1367px) {
    .home {
      padding-top: 0px;
    }

    .tagline {
      font-size: 2.7rem;
      line-height: 2.5rem;
    }
  
    .description {
      font-size: 1.7rem;
      line-height: 1.7rem;
    }
  
    button {
      width: 250px;
    }
  }

